import React from 'react'

export default function Work() {
    return (<div className="cont" id="work-page">
        <h1>Previous Works</h1>
        <div className="posts">
            <div className="post">
                <a href="/"><img src="/assets/works/1.png" alt="" /></a>
            </div>
            <div className="post">
                <img src="https://afaithraf.github.io/my-portfolio//assets/works/2.jpg" alt="" />
            </div>
        </div>
        <style jsx>{`
            .cont{
                width: 100%;
                position: relative;
               text-align: center;
               color: #ffffff;
               height: auto;
               background: #010101;
               padding: 30px;;
            }
            .posts{
                display: flex;
                justify-content: center;
                flex-wrap: wrap;
            }
            .post{
                width: 500px;
                padding-top: 100%;
                margin: 60px;
                border-radius: 16px;
                overflow: hidden;
                border: 1px solid #ffff00;
                cursor: default;
            }
            .post img{
                width: 100%;
                height: 100%;
                margin: 0;
                padding: 0;
                object-fit: cover;
            }
            `}</style>
    </div>
    )
}
